/* MARK: Hero */

.pre-mounted .heroRow {
}

.heroRow {
  height: 600px; 
}

.pre-mounted .heroRow .videoContainer {
  -webkit-transform: translateX(-200px);
          transform: translateX(-200px);
  opacity: 0;
}

.heroRow .videoContainer {
  transition: opacity 0.5s 0.4s, -webkit-transform 0.6s 0.4s;
  transition: transform 0.6s 0.4s, opacity 0.5s 0.4s;
  transition: transform 0.6s 0.4s, opacity 0.5s 0.4s, -webkit-transform 0.6s 0.4s;
  opacity: 1;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  height: 100%;
  display: inline-block;
  /* float: left; */
}

.videoContainer {
  overflow: hidden;
  width: calc(50% + (100vw - 1140px)/2);
  margin-left: calc((-100vw + 1140px)/2);
  /* transform: translateX(-50%) */
  /* width: 100%; */
}

.videoContainer .video {

}


.pre-mounted .video img {
  -webkit-transform: translateX(-40px) scaleX(-1);
          transform: translateX(-40px) scaleX(-1);
}

.video img {
  transition: -webkit-transform 0.8s 0.4s;
  transition: transform 0.8s 0.4s;
  transition: transform 0.8s 0.4s, -webkit-transform 0.8s 0.4s;
  min-width: 100%;
  min-height: 100%;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  /* width: 100%; */
}

.heroRow .heroTextContainer {
  display: inline-block;
  height: 100%;
  float: right;
}

.heroText {
  display: block;
  position: relative;
  margin: 0px 10% 50px 50px;
  top: 40%;
  -webkit-transform: translateY(-48%);
          transform: translateY(-48%);
}

.pre-mounted .heroText h1 span {
  -webkit-transform: translateY(100px);
          transform: translateY(100px); 
  opacity: 0;
}

.heroText h1 .indv-Span {
  overflow: hidden;
  display: inline-block;
  opacity: 1;
  transition: opacity 0.3s, -webkit-transform 0.6s 0s cubic-bezier(0.145, 0.555, 0.375, 1.125);
  transition: transform 0.6s 0s cubic-bezier(0.145, 0.555, 0.375, 1.125), opacity 0.3s;
  transition: transform 0.6s 0s cubic-bezier(0.145, 0.555, 0.375, 1.125), opacity 0.3s, -webkit-transform 0.6s 0s cubic-bezier(0.145, 0.555, 0.375, 1.125);
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
}

.heroText h1 {
  font-size: 48px;
  font-weight: bold;
}

.pre-mounted p {
  -webkit-transform: translateY(100px);
          transform: translateY(100px); 
  opacity: 0;
}

.heroText p {
  transition: opacity 0.3s 0.6s, -webkit-transform 0.6s 0.6s;
  transition: transform 0.6s 0.6s, opacity 0.3s 0.6s;
  transition: transform 0.6s 0.6s, opacity 0.3s 0.6s, -webkit-transform 0.6s 0.6s;
  /* margin-top: 60px; */
  width: 100%;
  font-size: 24px;
  line-height: 1.5;
}

.pre-mounted .heroText button {
  opacity: 0;
  -webkit-transform: translateY(100px);
          transform: translateY(100px); 
}

@-webkit-keyframes biggerIt {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes biggerIt {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  20% {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.heroText button {
  transition: opacity 0.3s 0.9s, -webkit-transform 0.6s 0.8s;
  transition: transform 0.6s 0.8s, opacity 0.3s 0.9s;
  transition: transform 0.6s 0.8s, opacity 0.3s 0.9s, -webkit-transform 0.6s 0.8s;
  -webkit-animation: greenStartBack 1s 1.8s;
          animation: greenStartBack 1s 1.8s;
  font-size: 24px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  padding: 6px 30px;
  border: none;
  background-color: #67DDAB;
}

.heroText button:hover {
  -webkit-animation: greenStartBack 0.5s infinite;
          animation: greenStartBack 0.5s infinite;
}

.heroText button:focus {
  outline: none;
  opacity: 1;
  border: none;
}

/* MARK: Summary Row */
.summaryRow {
  width: 100%;
  height: auto;
  transition: width 1s ease;
}

.postIts {
  margin-top: 15%;
  transition: opacity 1s;
  opacity: 0;
  vertical-align: top;
  width: 60%;
  padding: 80px 0 0px 80px;
  /* transform: translateY(-30%); */
}

.active .postIts {
  opacity: 1;
}

.summaryRow img {
  width: 100%;
}

.summaryBackground {
  position: absolute;
  z-index: -3;
  left: 0px;
  margin-top: -60px;
  margin-bottom: 100px;
  height: 120%;
  width: 100vw;

  background-repeat: no-repeat;
  background-image: linear-gradient(#8ECCEC, #8ECCEC);
  background-size: 0% 100%;
  transition: background-size 0.8s;
}

.active .summaryBackground {
  background-size: 100% 100%;
}

.summaryBackground svg {
  width: 1440px;
  z-index: -1;
  float: left;
  margin-top: -120px;
  margin-right: -80px;
  margin-left: calc(70px + 30%);
  position: absolute;
}

.summaryBackground path {
  fill: none;
  stroke-width: 2px;
  stroke-dasharray: 10px;
  transition: stroke-linecap 1s, stroke-width 1s;
}

.summaryBackground path {
  stroke: white;
  stroke-dasharray: 1740px;
  stroke-dashoffset: 0px;
  transition: stroke-dashoffset 3s 0.4s, stroke-dasharray 2s 0.4s;
}

.summaryRow path {
  stroke-dashoffset: 1740px;
}

.inspiration, .workshop {
  z-index: -10;
  opacity: 0.3;
}

.popUp {
  z-index: -1;
  opacity: 0.6;
}

.inspiration svg, .workshop svg, .popUp svg {
  z-index: -5;
  margin-left: 0;
  width: 104%;
  margin-top: 0%;
}

.workshop svg {
  margin-top: -10%;
}

.inspiration path {
  stroke: #FF9C1C;
}

.workshop path {
  stroke: #67DDAB;
}
.popUp svg {
  margin-top: -240px;
}

.popUp path {
  stroke: #8ECCEC;
}

.inspiration .backPath path, .workshop .backPath path, .popUp .backPath path {
  stroke: white;
}


.active path {
  stroke-dashoffset: 0px;
  /* stroke-dasharray: 10px; */
}

.summaryRow .backPath path {
  opacity: 0;
  transition: opacity 0s 0.7s;
}

.active .backPath path {
  opacity: 1;
}

.backPath path {
  opacity: 1;
  stroke: #8ECCEC;
  stroke-width: 4px;
  stroke-dasharray: 10px;
  stroke-dashoffset: 20px;
  transition: opacity 0.2s 0.4s;
  -webkit-animation: pathSlow 3.2s linear infinite;
          animation: pathSlow 3.2s linear infinite; 
}

.active .backPath path {
  opacity: 1;
}

@-webkit-keyframes pathSlow {
  100% {
    stroke-dashoffset: 0px;
  }
}

@keyframes pathSlow {
  100% {
    stroke-dashoffset: 0px;
  }
}

.summary {
  display: inline-block;
  width: 40%; 
  margin-top: 15%;
  opacity: 0;
  /* margin-top: 50%; */
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  transition: opacity 1s, -webkit-transform 1s;
  transition: transform 1s, opacity 1s;
  transition: transform 1s, opacity 1s, -webkit-transform 1s;
}

.summary h2 {
  font-size: 24px;
  font-weight: bold;
  color: white;
}

.summary h1 {
  font-size: 48px;
  font-weight: bold;
}

.summary p {
  margin-top: 30px;
  font-size: 24px;
}

.active .summary {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

/* MARK: Workshop Cards */
.lightbulbAnimation {
  width: 600px;
  display: block;
  margin: auto;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
  overflow: hidden;
  padding-top: 240px;
  margin-top: -200px;
  margin-bottom: -100px;
}

.emailContainer a {
  color: #67DDAB;
  /* border-bottom: black 2px solid; */
  border-bottom: #8ECCEC 4px solid;
  transition: padding 0.1s;
}

.emailContainer a:hover {
  /* opacity: 0.5; */
  text-decoration: none;
  color: #67DDAB;
  border-color: #67DDAB;
  border-bottom: #FF9C1C 4px solid;
  padding-bottom: 3px;
  -webkit-animation: greenStart 0.5s infinite;
          animation: greenStart 0.5s infinite;
}

@-webkit-keyframes greenStart {
  0% {
    color: #8ECCEC;
  }
  50% {
    color: #FF9C1C;
  }
  100% {
    color: #67DDAB;
  }
}

@keyframes greenStart {
  0% {
    color: #8ECCEC;
  }
  50% {
    color: #FF9C1C;
  }
  100% {
    color: #67DDAB;
  }
}

@-webkit-keyframes greenStartBack {
  0% {
    background: #8ECCEC;
  }
  50% {
    background: #FF9C1C;
  }
  100% {
    background: #67DDAB;
  }
}

@keyframes greenStartBack {
  0% {
    background: #8ECCEC;
  }
  50% {
    background: #FF9C1C;
  }
  100% {
    background: #67DDAB;
  }
}

@-webkit-keyframes spriteSheet-6frame {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(calc(-605.1px*6));
            transform: translateX(calc(-605.1px*6));
  }
}

@keyframes spriteSheet-6frame {
  0% {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  100% {
    -webkit-transform: translateX(calc(-605.1px*6));
            transform: translateX(calc(-605.1px*6));
  }
}

.lightbulbAnimation img:hover {
  -webkit-animation: spriteSheet-6frame 0.5s steps(6);
          animation: spriteSheet-6frame 0.5s steps(6);
}

.lightbulbAnimation img {
  height: 100%;
  /* margin-left: -100px; */
  /* transform: translateX(-605px); */
}

