    transition: transform 0.6s 0.8s;
    transition: transform 0.6s 0.8s, -webkit-transform 0.6s 0.8s;
}

.Contact.submitted .sentContainer {
    /* display: block; */
    visibility: visible;
    opacity: 1;
}

.Contact.submitted .formContainer {
    /* display: none; */
}

.form a {
